
.container-content {
    display: flex;
    flex-direction: column;
}

.content-right {
    width: 100px;
    height: 100px;
}

.content-left {
    background-color: #E9EBEE;
    position: fixed;
    left: 0;
    bottom: 0;
    top: 56px;
    width: 72px;
}

.content-left:hover {
    width: 250px;
}

.content-left .container {
    padding-top: 16px;
    padding-bottom: 16px;
    /* padding: 8px; */
}

.content-left .item-nav {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.nav-user {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.nav-Content .nav-user {
    display: flex;
    justify-content: start;
}

.nav-user .container {
    font-size: 10px;
}

.nav-Content .avatar-circle {
    vertical-align: middle;
    width: 56px;
    height: 56px;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
}


.nav-Content .avatar-nav {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: 4px solid blue;
}

.nav-Content .menu {
    margin: 16px 0;
    font-size: 24px;
    display: flex;
    flex-direction: column;
}

.nav-Content .menu .item-nav {
    padding: 8px;
    display: flex;
    flex-direction: row;
    align-items: center;
    /* padding-right: 50px; */
    cursor: pointer;
}

.nav-Content .menu .item-nav p {
    flex: 1 1 1px;
    font-size: 16px;
    width: 100px;
}

.nav-Content .menu .item-nav .svg-inline--fa {
    display: flex;
    flex-direction: row;
    color: #1a1a1a;
    /* width: 30px !important;
    margin-right: 24px;
    margin-left: 8px;*/
    align-items: center;
    font-size: 20px;
    width: 40px;
}

.nav-Content .menu .item-nav:hover {
    background-color: aqua;
    border-radius: 8px;
}

.nav-services .Header-service span {
    font-size: 16px;
    font-weight: 600;
}

.nav-services .container-services {
    background-color: aliceblue;
    padding: 10px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
}

.nav-services .container-services .item-service {
    color: #1a1a1a;
    border-radius: 20%;
    padding: 4px;
    font-size: 16px;
    display: flex;
    flex-direction: row;
}

.nav-services .container-services .item-service .svg-inline--fa {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-right: 24px;
    font-size: 20px;
    width: 30px
}

/* .nav-Content .container-services .item-nav p {
    font-size: 16px;
    width: 100px;
} */

.nav-services .container-services .item-service p {
    flex: 1 1 1px;
    font-size: 16px;
}

.nav-services .container-services .item-service:hover {
    background-color: aqua;
    border-radius: 20%;
}

