@import '../../css/helpers/variables.css';

/* css header  */
.header-vieme {
    background-color: #4169E1;
    width: 100%;
    height: var(--heightHeader);
    display: block;
}

/*  css container header */

.container-Header {
    padding-left: var(--marginHeader);
    padding-right: var(--marginHeader);
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
}

/* css header left */

.header-vieme .header-left {
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: start;
}

.header-vieme .logo-circle {
    width: 56px;
    height: 56px;
    vertical-align: middle;
    border-radius: 50%;
}

.header-vieme .logo-header {
    border-radius: 50%;
    padding: 6px;
    width: 100%;
    height: 100%;
}


.header-vieme .header-left .Name-Company {
    margin-right: 8px;
}

.header-vieme .header-left .Name-Company h1{
    color: #ffffff;
    font-size: 26px;
    display: flex;
    flex-direction: row;
    align-items: center;
}

/* css search form - center header */

.header-vieme .header-center {
    width: 500px;
    display: flex;
    align-items: center;
    flex-direction: row;
}

.header-vieme .SearchForm {
    display: flex;
    align-items: center;
    flex-direction: row;
    width: 100%;
}

.header-center .item-centerHeader {
    display: none;
}

/* css header right */

.header-vieme .header-right {
    display: flex;
    justify-content: end;
    align-items: center;
    flex-direction: row;
}

.header-vieme .header-right .svg-inline--fa {
    color: #fff;
    font-size: var(--font-SizeIconHeader);
    margin: 0 var(--margin-IconHeader) 0 var(--margin-IconHeader);
    color: var(--coloriconHeader);
}

.header-right .avatar-circle {
    width: 30px;
    height: 30px;
    vertical-align: middle;
    border-radius: 50%;
    display: flex;
    align-items: center;
    margin: 0 var(--margin-IconHeader) 0 var(--margin-IconHeader);
    flex-direction: row;
}

.header-right .avatar-header {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    display: flex;
    align-items: center;
    flex-direction: row;
}

